const app =getApp()
import helper from '../componets/helper.js'
import MescrollMixin from '../componets/mescroll-uni/mescroll-mixins.js';
export default {
	mixins: [MescrollMixin], // 使用mixin
	data() {
		return {
			Inv: 0,
			isSAutoplay:  false,
			curricula_id: '',
			nav: [{ navName: '课程详情' }, { navName: '课程章节' }, { navName: '课程评价' }],
			imgList: [{ img: '../static/nn.png' }, { img: '../static/nn.png' }],
			commentList:[],
			content: {},
			imgUrl: '',
			videoUrl: '',
			downOption: {
				auto: true //是否在初始化后,自动执行downCallback; 默认true
			},
			upOption: {
				page: {
					num: 0, // 当前页码,默认0,回调之前会加1,即callback(page)会从1开始
					size: 20 // 每页数据的数量,默认10
				},
				noMoreSize: 5, // 配置列表的总数量要大于等于5条才显示'-- END --'的提示
				empty: {
					tip: '暂无相关数据'
				},
				textNoMore: '没有更多数据了'
			},
			buyShow: '',
			isShowBox: ''
		};
	},
	onLoad(option) {
		this.imgUrl = helper.imgUrl
		this.curricula_id = option.id
		this.getList()
		uni.request({
			url: `${helper.requestUrl}discover/hide`,
			method: 'GET',
			success: res => {
				res = helper.null2str(res.data);
				if (res.status_code == 'ok') {
					console.log(res.data)
					this.isShowBox = res.data;
				}
			}
		});
	},
	onShow() {
		
	},
	methods: {
		navTap(e) {
			this.Inv = e;
		},
		getList(){
			uni.showLoading({
				title: '加载中...',
			})
			uni.request({
				url: `${helper.requestUrl}details/curricula`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data:{
					curricula_id: this.curricula_id
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res.data);
					console.log(res)
					if (res.status_code == 'ok') {
						this.content = res.data;
						this.buyShow = res.is_buy
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		},
		convert(e){
			uni.navigateTo({
				url:`/pages/courseCode?id=${e}`
			})
		},
		// 购买课程
		goBuy(e){
			uni.navigateTo({
				url:`/pages/dataBuy?type=courseBuy&id=${e}`
			})
		},
		// 视频播放
		showVideo(id,e){
				uni.request({
					url: `${this.$helper.requestUrl}details/getVideoUrl`,
					method: 'POST',
					header: {
						authorization: app.globalData.token
					},
					data:{
						id: id
					},
					success: res => {
						uni.hideLoading();
						res = this.$helper.null2str(res.data);
						console.log(res)
						if (res.status_code == 'ok') {
							this.isSAutoplay = true
							this.videoUrl = res.data
						} else {
							uni.showToast({
								title: res.message,
								icon: 'none'
							});
						}
					}
				});
			
		},
		/*下拉刷新的回调 */
		downCallback() {
			this.mescroll.resetUpScroll();
		},
		upCallback(page) {
			this.getCommitList(page)
		},
		getCommitList(page){
			uni.showLoading({
				title: '加载中...',
			})
			uni.request({
				url: `${this.$helper.requestUrl}details/evaluateList`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data:{
					id: this.curricula_id,
					type: 'curricula',
					page_size: page.size,
					page: page.num
				},
				success: res => {
					uni.hideLoading()
					res = this.$helper.null2str(res.data);
					if (res.status_code == 'ok') {
						if(res.message == '暂无信息'){
							this.commentList = []
							this.mescroll.endByPage(0, 0);
						}else{
							if (page.num == 1) this.commentList = [];
							this.commentList = this.commentList.concat(res.data.data);
							this.mescroll.endByPage(res.data.data.length,  res.data.total);
						}
					} else {
						
					}
				}
			});
		},


	}
};
